import React, {Component} from "react";
import {MyMovies} from "./myMovies";

export const WishesList = () => {
    return (
        <>
            <MyMovies wishlist={true}
                      type={"Ksika" || "Film"}
                      pageName="Lista ycze"
                      movieDate={"Wyszukaj od"}/>
        </>
    )
};