import React, {Component} from "react";
import {MyMovies} from "./myMovies";

export const MyShelf = () => {
    return (
        <>
            <MyMovies
                wishlist={false}
                type="Ksika"
                pageName="Moja Pka"
                movieDate="Wyszukaj Przeczytane od"
                popularityAuthor="Autor: "
                voteAveragePages ="Ilo Stron: "
                voteCountLanguage="Jzyk: "
            />
        </>
    )
};